@mixin mQ($arg...) {
    @media screen and (min-width:nth($arg, 1)) and (max-width: nth($arg, 2)) {
        @content;
    }
}
.contactForm {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    border: 2px solid white;
    padding: 10px;
    width: 80vw;
    position: relative;
    top:40vh;
}

.contactForm div {
    text-align: center;
    margin:0 auto;
    width:100%;
    padding: 10px;
}

.contactForm div label {
    font-weight: 400;
    color:white;
    font-size: 1rem;
}